import React, { useState } from "react";
import { graphql, navigate } from "gatsby";
import { signInWithEmailAndPassword } from "firebase/auth";
import { auth } from "../firebaseConfig";
import Layout from "./layout";
import { Company } from "../interfaces";
import data from "../data/data.json";

const info: Company = data[0];

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await signInWithEmailAndPassword(auth, email, password);
      navigate("/user" as unknown as number); // Redirige al área protegida si el inicio de sesión es exitoso
    } catch (error) {
      setError("Credenciales incorrectas. Por favor, intenta de nuevo.");
    }
  };

  return (
    <Layout data={info}>

    <div>
      <form onSubmit={handleLogin} className={styles.container} id="login">
        <h1 className={styles.title}>Iniciar Sesión</h1>
        <div className={styles.groupItem}>
          <label htmlFor="email" className={styles.label}>Email</label>
            <input
            type="email"
            id="email"
            placeholder="Correo Electrónico"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
            className={styles.input}
            />
        </div>
        <div className={styles.groupItem}>
          <label htmlFor="password" className={styles.label}>Contraseña</label>        
            <input
            type="password"
            id="password"
            placeholder="Contraseña"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className={styles.input}
            required
            />
          </div>
          <div className={styles.groupItem}>
            <button className={styles.submitButton} type="submit">Iniciar Sesión</button>
          </div>
      </form>
      {error && <p className={styles.message}>{error}</p>}
    </div>
</Layout>
  );
};

export default Login;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;

const styles = {
    container: "bg-secondary p-8 rounded-lg shadow-md w-full max-w-md mx-auto my-8",
    title: "text-2xl font-bold mb-6 text-dark text-center",
    label: "block text-dark font-medium",
    input: "w-full text-dark px-4 py-2 border border-terciary rounded-lg focus:outline-none focus:ring-2 focus:ring-primary",
    submitButton: "w-full bg-dark text-white py-2 px-4 rounded-lg hover:bg-terciary hover:text-dark transition",
    groupItem: "mb-4",
    message: "mt-4 text-center text-dark font-medium"
  };
